<template>
  <div id="app">
    <h1>Базовые операции</h1>

    <div class="operation">
      <h2 @click="toggleForm('filterSortApartments')" class="blue-header">
        Получить список квартир с фильтрацией, сортировкой и постраничным выводом
      </h2>
      <FilterSortApartmentForm v-if="activeForm === 'filterSortApartments'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('addApartment')" class="green-header">
        Добавить новую квартиру
      </h2>
      <AddApartmentForm v-if="activeForm === 'addApartment'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('getApartment')" class="blue-header">
        Получить квартиру по ID
      </h2>
      <GetApartmentByIdForm v-if="activeForm === 'getApartment'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('updateApartment')" class="yellow-header">
        Обновить квартиру по ID
      </h2>
      <UpdateApartmentForm v-if="activeForm === 'updateApartment'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('deleteApartment')" class="red-header">
        Удалить квартиру по ID
      </h2>
      <DeleteApartmentForm v-if="activeForm === 'deleteApartment'" @close="closeForm" />
    </div>


    <h1>Дополнительные операции</h1>

    <div class="operation">
      <h2 @click="toggleForm('numberOfRoomsApartment')" class="blue-header">
        Рассчитать среднее значение поля numberOfRooms для всех квартир
      </h2>
      <AverageNumberOfRooms v-if="activeForm === 'numberOfRoomsApartment'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('TransportCountApartment')" class="blue-header">
        Вернуть количество квартир с transport больше заданного значения
      </h2>
      <TransportCount v-if="activeForm === 'TransportCountApartment'" @close="closeForm" />
    </div>

    <div class="operation">
      <h2 @click="toggleForm('FurnishFilterApartment')" class="blue-header">
        Вернуть массив квартир с furnish больше заданного значения
      </h2>
      <FurnishFilter v-if="activeForm === 'FurnishFilterApartment'" @close="closeForm" />
    </div>

  </div>
</template>

<script>
import AddApartmentForm from './components/AddApartmentForm.vue';
import DeleteApartmentForm from "@/components/DeleteApartmentForm.vue";
import GetApartmentByIdForm from "@/components//GetApartmentByIdForm.vue";
import FilterSortApartmentForm from "@/components/FilterSortApartmentForm.vue";
import UpdateApartmentForm from "@/components/UpdateApartmentForm.vue";
import AverageNumberOfRooms from "@/components/AverageNumberOfRooms.vue";
import TransportCount from "@/components/TransportCount.vue";
import FurnishFilter from "@/components/FurnishFilter.vue";

export default {
  components: {
    DeleteApartmentForm,
    AddApartmentForm,
    GetApartmentByIdForm,
    FilterSortApartmentForm,
    UpdateApartmentForm,
    AverageNumberOfRooms,
    TransportCount,
    FurnishFilter
  },
  data() {
    return {
      activeForm: null
    };
  },
  methods: {
    toggleForm(formName) {
      this.activeForm = this.activeForm === formName ? null : formName;
    },
    closeForm() {
      this.activeForm = null;
    }
  }
};
</script>

