<!-- src/components/AverageNumberOfRooms.vue -->
<template>
  <div class="average-rooms">
    <h2>Среднее значение numberOfRooms:</h2>
    <p v-if="averageNumberOfRooms !== null">{{ averageNumberOfRooms }}</p>
    <p v-else>Загрузка...</p>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
import apiClient from '../apiClient';

export default {
  data() {
    return {
      averageNumberOfRooms: null,
      errorMessage: ''
    };
  },
  mounted() {
    this.fetchAverageNumberOfRooms();
  },
  methods: {
    async fetchAverageNumberOfRooms() {
      this.errorMessage = '';
      try {
        const response = await apiClient.get('/flats/average-number-of-rooms', {
          headers: {'Accept': 'application/xml'}
        });

        if (response.status === 200) {
          // Парсинг XML и извлечение значения
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(response.data, 'application/xml');
          const averageRooms = xmlDoc.getElementsByTagName('Double')[0]?.textContent;

          this.averageNumberOfRooms = parseFloat(averageRooms); // Преобразование к числу
        }
      } catch (error) {
        const status = error.response ? error.response.status : null;
        switch (status) {
          case 404:
            this.errorMessage = 'Ошибка: данные не найдены.';
            break;
          case 500:
            this.errorMessage = 'Ошибка сервера. Попробуйте позже.';
            break;
          default:
            this.errorMessage = 'Произошла непредвиденная ошибка.';
        }
      }
    }
  }
};
</script>

<style scoped>
.average-rooms {
  margin: 20px auto;
  padding: 20px;
  max-width: 400px;
  background-color: #f7f7f7;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

h2 {
  color: #333;
  margin-bottom: 15px;
}

.error-message {
  color: #ff4d4f;
  font-weight: bold;
}
</style>
