<!-- src/components/FurnishFilter.vue -->
<template>
  <div class="furnish-filter">
    <form @submit.prevent="fetchFilteredFlats">
      <div class="form-group">
        <label for="furnish">Выберите furnish</label>
        <select id="furnish" v-model="selectedFurnish" required>
          <option disabled value="">Выберите значение</option>
          <option value="DESIGNER">Дизайнер</option>
          <option value="FINE">Хороший</option>
          <option value="BAD">Плохой</option>
          <option value="LITTLE">Немного</option>
        </select>
      </div>
      <button type="submit" class="submit-button">Получить квартиры</button>
    </form>

    <div v-if="apartment && apartment.length" class="apartments-list">
      <h3>Квартиры с furnish больше "{{ selectedFurnish }}":</h3>
      <div v-for="apt in apartment" :key="apt.id">
        <table>
          <tbody>
          <tr><td><strong>ID:</strong></td><td>{{ apt.id }}</td></tr>
          <tr><td><strong>Название:</strong></td><td>{{ apt.name }}</td></tr>
          <tr><td><strong>Координаты:</strong></td><td>X: {{ apt.coordinates.x }}, Y: {{ apt.coordinates.y }}</td></tr>
          <tr><td><strong>Дата создания:</strong></td><td>{{ apt.creationDate }}</td></tr>
          <tr><td><strong>Площадь:</strong></td><td>{{ apt.area }} м²</td></tr>
          <tr><td><strong>Кол-во комнат:</strong></td><td>{{ apt.numberOfRooms }}</td></tr>
          <tr><td><strong>Отделка:</strong></td><td>{{ apt.furnish }}</td></tr>
          <tr><td><strong>Вид:</strong></td><td>{{ apt.view }}</td></tr>
          <tr><td><strong>Транспорт:</strong></td><td>{{ apt.transport }}</td></tr>
          <tr><td><strong>Дом:</strong></td>
            <td>
              {{ apt.house.name }}, Год: {{ apt.house.year }},
              Этажи: {{ apt.house.numberOfFlatsOnFloor }},
              Лифты: {{ apt.house.numberOfLifts }}
            </td>
          </tr>
          <tr><td><strong>Цена:</strong></td><td>{{ apt.price }} ₽</td></tr>
          </tbody>
        </table>
      </div>
    </div>
    <p v-else-if="apartment.length === 0 && selectedFurnish" class="no-results">
      Квартиры с furnish "{{ selectedFurnish }}" не найдены.
    </p>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>



<script>
import apiClient from '../apiClient';

export default {
  data() {
    return {
      selectedFurnish: '',
      apartment: [],
      errorMessage: ''
    };
  },
  methods: {
    async fetchFilteredFlats() {
      this.errorMessage = '';
      this.apartment = []; // изменяем, чтобы это был массив для всех квартир
      try {
        const response = await apiClient.get(`/flats/filter-by-furnish`, {
          params: { furnish: this.selectedFurnish },
          headers: { 'Content-Type': 'application/xml' }
        });

        if (response.status === 200) {
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(response.data, 'application/xml');
          const items = xmlDoc.getElementsByTagName("item"); // Получаем все элементы item

          // Парсим каждый item и добавляем в массив apartments
          this.apartment = Array.from(items).map(item => this.parseApartmentXML(item));
        }
      } catch (error) {
        const status = error.response ? error.response.status : null;
        switch (status) {
          case 400:
            this.errorMessage = 'Неверный параметр запроса. Проверьте выбранное значение.';
            break;
          case 404:
            this.errorMessage = 'Запрашиваемый ресурс не найден.';
            break;
          case 500:
            this.errorMessage = 'Ошибка сервера. Попробуйте позже.';
            break;
          default:
            this.errorMessage = 'Произошла непредвиденная ошибка.';
        }
        console.error("Error fetching flats:", this.errorMessage);
      }
    },

    parseApartmentXML(item) {
      return {
        id: item.getElementsByTagName("id")[0].textContent,
        name: item.getElementsByTagName("name")[0].textContent,
        coordinates: {
          x: item.getElementsByTagName("x")[0].textContent,
          y: item.getElementsByTagName("y")[0].textContent
        },
        creationDate: item.getElementsByTagName("creationDate")[0].textContent,
        area: item.getElementsByTagName("area")[0].textContent,
        numberOfRooms: item.getElementsByTagName("numberOfRooms")[0].textContent,
        furnish: item.getElementsByTagName("furnish")[0].textContent,
        view: item.getElementsByTagName("view")[0].textContent,
        transport: item.getElementsByTagName("transport")[0].textContent,
        house: {
          name: item.getElementsByTagName("name")[1].textContent,
          year: item.getElementsByTagName("year")[0].textContent,
          numberOfFlatsOnFloor: item.getElementsByTagName("numberOfFlatsOnFloor")[0].textContent,
          numberOfLifts: item.getElementsByTagName("numberOfLifts")[0].textContent
        },
        price: item.getElementsByTagName("price")[0].textContent
      };
    }
  }
}
</script>



<style scoped>
.furnish-filter {
  max-width: 600px;
  margin: 20px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

select, button {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
}

.submit-button {
  background: linear-gradient(135deg, #25d6d6, #2b18a3);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin-top: 15px;
}

.flats-list {
  margin-top: 20px;
  color: #333;
}

.error-message {
  color: #ff4d4f;
  font-weight: bold;
  margin-top: 10px;
}
</style>
