<template>
  <div class="transport-count">
    <form @submit.prevent="fetchFlatCount">
      <div class="form-group">
        <label for="transport">Выберите транспорт</label>
        <select id="transport" v-model="selectedTransport" required>
          <option disabled value="">Выберите значение</option>
          <option value="FEW">Мало</option>
          <option value="NONE">Нет</option>
          <option value="NORMAL">Обычный</option>
          <option value="ENOUGH">Достаточно</option>
        </select>
      </div>
      <button type="submit" class="submit-button">Получить количество квартир</button>
    </form>
    <p v-if="count !== null" class="result-message">Количество квартир с транспортом больше "{{ selectedTransport }}": {{ count }}</p>
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
import apiClient from '../apiClient';

export default {
  data() {
    return {
      selectedTransport: '',
      count: null,
      errorMessage: ''
    };
  },
  methods: {
    async fetchFlatCount() {
      this.errorMessage = '';
      this.count = null;

      try {
        const response = await apiClient.get(`/flats/count-by-transport`, {
          params: { transport: this.selectedTransport },
          headers: { 'Content-Type': 'application/xml' }
        });

        if (response.status === 200) {
          this.count = response.data;
        }
      } catch (error) {
        const status = error.response ? error.response.status : null;
        switch (status) {
          case 400:
            this.errorMessage = 'Неверный параметр запроса. Проверьте выбранное значение.';
            break;
          case 404:
            this.errorMessage = 'Запрашиваемый ресурс не найден.';
            break;
          case 500:
            this.errorMessage = 'Ошибка сервера. Попробуйте позже.';
            break;
          default:
            this.errorMessage = 'Произошла непредвиденная ошибка.';
        }
      }
    }
  }
};
</script>

<style scoped>
.transport-count {
  max-width: 600px;
  margin: 20px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

select, button {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1rem;
}

.submit-button {
  background: linear-gradient(135deg, #25d6d6, #2b18a3);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin-top: 15px;
}

.error-message, .result-message {
  margin-top: 10px;
  font-weight: bold;
  text-align: center;
}

.error-message {
  color: #ff4d4f;
}

.result-message {
  color: #42b983;
}
</style>
