<!-- src/components/GetApartmentByIdForm.vue -->
<template>
  <div class="get-apartment-form">
    <form @submit.prevent="fetchApartment">
      <div class="form-group">
        <label for="apartmentId">ID Квартиры</label>
        <input
            type="number"
            id="apartmentId"
            v-model="apartmentId"
            required
            min="1"
            placeholder="Введите ID"
        />
      </div>
      <button type="submit" class="submit-button">Вывести информацию о квартире</button>
      <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
    </form>

    <div v-if="apartment" class="apartment-info">
      <h3>Информация о квартире</h3>
      <table>
        <tbody>
        <tr><td><strong>ID:</strong></td><td>{{ apartment.id }}</td></tr>
        <tr><td><strong>Название:</strong></td><td>{{ apartment.name }}</td></tr>
        <tr><td><strong>Координаты:</strong></td><td>X: {{ apartment.coordinates.x }}, Y: {{ apartment.coordinates.y }}</td></tr>
        <tr><td><strong>Дата создания:</strong></td><td>{{ apartment.creationDate }}</td></tr>
        <tr><td><strong>Площадь:</strong></td><td>{{ apartment.area }} м²</td></tr>
        <tr><td><strong>Кол-во комнат:</strong></td><td>{{ apartment.numberOfRooms }}</td></tr>
        <tr><td><strong>Отделка:</strong></td><td>{{ apartment.furnish }}</td></tr>
        <tr><td><strong>Вид:</strong></td><td>{{ apartment.view }}</td></tr>
        <tr><td><strong>Транспорт:</strong></td><td>{{ apartment.transport }}</td></tr>
        <tr><td><strong>Дом:</strong></td>
          <td>
            {{ apartment.house.name }}, Год: {{ apartment.house.year }},
            Этажи: {{ apartment.house.numberOfFlatsOnFloor }},
            Лифты: {{ apartment.house.numberOfLifts }}
          </td>
        </tr>
        <tr><td><strong>Цена:</strong></td><td>{{ apartment.price }} ₽</td></tr>
        </tbody>
      </table>
    </div>
  </div>
</template>


<script>
import apiClient from '../apiClient';

export default {
  data() {
    return {
      apartmentId: '',
      apartment: null,
      errorMessage: '',
    };
  },
  methods: {
    async fetchApartment() {
      this.errorMessage = '';
      this.apartment = null;

      try {
        const response = await apiClient.get(`/flats/${this.apartmentId}`, {
          headers: { 'Accept': 'application/xml' },
        });

        if (response.status === 200) {
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(response.data, 'application/xml');
          this.apartment = this.parseApartmentXML(xmlDoc);
        }
      } catch (error) {
        const status = error.response ? error.response.status : null;
        switch (status) {
          case 404:
            this.errorMessage = 'Квартира с таким ID не существует.';
            break;
          case 400:
            this.errorMessage = 'Неверный ввод. Проверьте ID и попробуйте снова.';
            break;
          case 500:
            this.errorMessage = 'Ошибка сервера. Попробуйте позже.';
            break;
          default:
            this.errorMessage = 'Произошла непредвиденная ошибка.';
        }
      }
    },
    parseApartmentXML(xml) {
      const parseNode = (node) => node ? node.textContent : '';
      return {
        id: parseNode(xml.querySelector('id')),
        name: parseNode(xml.querySelector('name')),
        coordinates: {
          x: parseNode(xml.querySelector('coordinates > x')),
          y: parseNode(xml.querySelector('coordinates > y')),
        },
        creationDate: parseNode(xml.querySelector('creationDate')),
        area: parseNode(xml.querySelector('area')),
        numberOfRooms: parseNode(xml.querySelector('numberOfRooms')),
        furnish: parseNode(xml.querySelector('furnish')),
        view: parseNode(xml.querySelector('view')),
        transport: parseNode(xml.querySelector('transport')),
        house: {
          id: parseNode(xml.querySelector('house > id')),
          name: parseNode(xml.querySelector('house > name')),
          year: parseNode(xml.querySelector('house > year')),
          numberOfFlatsOnFloor: parseNode(xml.querySelector('house > numberOfFlatsOnFloor')),
          numberOfLifts: parseNode(xml.querySelector('house > numberOfLifts')),
        },
        price: parseNode(xml.querySelector('price')),
      };
    },
  },
};
</script>


<style scoped>
.get-apartment-form {
  max-width: 600px;
  margin: 20px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
}

input, button {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-top: 5px;
}

.submit-button {
  background: linear-gradient(135deg, #25d6d6, #2b18a3);
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  border: none;
  padding: 15px;
  border-radius: 8px;
  font-size: 1.1rem;
}

.error-message {
  color: #ff4d4f;
  font-weight: bold;
  margin-top: 10px;
}

.apartment-info {
  margin-top: 20px;
  padding: 15px;
  background: #f9f9f9;
  border-radius: 8px;
}

.apartment-info table {
  width: 100%;
  border-collapse: collapse;
}

.apartment-info table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
}

.apartment-info table td strong {
  font-weight: bold;
  color: #333;
}

</style>
